import React, { useEffect, useState , Component  } from 'react';
import ReactDOM from 'react-dom/client';
import './App.css';  

function App() {
 
  
  const [weatherData, setWeather] = useState('');

  const [weatherDataMain, setWeatherMain] = useState('');

  const [showModal, setShowModal] = useState(true);
  const [latvalue, setLatvalue] = useState('N/A');
  const [longvalue, setLongvalue] = useState('N/A');

  const monthNames = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December" ];
  var days = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'];

  
  var options = {
    enableHighAccuracy: true,
    timeout: 5000,
    maximumAge: 0,
  };
  function success(pos) {
    var crd = pos.coords;
    
    setLatvalue(`${crd.latitude}`);
    setLongvalue(`${crd.longitude}`);

    setShowModal(false);  

  }
  
  function errors(err) {
    console.warn(`ERROR(${err.code}): ${err.message}`);    
    setShowModal(true);
  }

  function getlocpopup() {
    if (navigator.geolocation) {
      navigator.permissions
        .query({ name: "geolocation" })
        .then(function (result) {
          if (result.state === "granted") { 
            navigator.geolocation.getCurrentPosition(success);
            setShowModal(false);
          } else if (result.state === "prompt") {
            navigator.geolocation.getCurrentPosition(success, errors, options);
          } else if (result.state === "denied") {
            setLatvalue('---');
            setLongvalue('---');
            setShowModal(true);
          }
          result.onchange = function () {
          
          };
        });
    } else {
      
    }
  }

  

  useEffect(() => {
    const loadPost = async () => { 

        const response = await fetch(`https://api.openweathermap.org/data/2.5/weather?lat=${latvalue}&lon=${longvalue}&appid=b88abe27d6169857e5493890573e64cc&units=metric`);
        const jsonRes = await response.json();
         
        setWeather(jsonRes);
        setWeatherMain(jsonRes.main); 

        console.log(jsonRes)  
    }
 
    loadPost();
}, [latvalue]); 




  return (
    <div className="App"> 

      { showModal ? 
      <>
        <div className='modalwrap'>
          <div className='modalwrap-inner'>
            <div className='select-location-holder'>
              <input type="text" placeholder='Enter your location...' />
              <button className='sub-btn'><i className="fa-solid fa-magnifying-glass"></i></button>
              <button className='sub-btn-auto' onClick={getlocpopup}><i className="fa-solid fa-location-dot"></i> Select location automatically</button>
            </div>
          </div>
        </div>
      </> 
      : null }

        <div className="container">
          <div className="weather-side">
            <div className="weather-gradient"></div>
            <div className="date-container"> 
              <h2 className="date-dayname">{ days[new Date().getDay()] } </h2><span className="date-day">{ new Date().getDate() } { monthNames[new Date().getMonth()] }, { new Date().getFullYear() }</span>
              <span className="location city-name">{weatherData.name}, { weatherData.sys ? weatherData.sys.country : ''}
                <div className="latlong">
                  <div className='latlong-inner'>
                    <span className="name">Latitude</span>
                    <span className="value">{ weatherData.coord ? weatherData.coord.lat : latvalue }</span>
                  </div>
                  <div className='latlong-inner'>
                    <span className="name">Longitude</span> 
                    <span className="value">{ weatherData.coord ? weatherData.coord.lon : longvalue }</span>
                  </div>
                </div>
              </span>

            </div>
            <div className="weather-container"><i className="weather-icon" data-feather="sun"></i>
              <h1 className="weather-temp">{ weatherData.main ? weatherData.main.temp : ''}<span>°C</span></h1>
              <h3 className="weather-desc">{ weatherData.weather ? weatherData.weather[0].description : ''}</h3>
            </div>
          </div>
          <div className="info-side">
            <div className="today-info-container">
              <div className="today-info">
              <div className="precipitation"> <span className="title">Feels Like</span><span className="value">{ weatherData.main ? weatherData.main.feels_like : ''} °C</span>
                  <div className="clear"></div>
                </div>
                <div className="precipitation"> <span className="title">Pressure</span><span className="value">{ weatherData.main ? weatherData.main.pressure : ''} mb</span>
                  <div className="clear"></div>
                </div>
                <div className="humidity"> <span className="title">Humidity</span><span className="value">{ weatherData.main ? weatherData.main.humidity : ''}  %</span>
                  <div className="clear"></div>
                </div>
                <div className="wind"> <span className="title">Wind</span><span className="value">{ weatherData.wind ? weatherData.wind.speed : ''} km/h</span>
                  <div className="clear"></div>
                </div>
              </div>
            </div>
            <div className="week-container">
              <ul className="week-list">
                <li className='active'><i className="day-icon" data-feather="sun"></i><span className="day-name">Sun</span><span className="day-temp">xx°C</span></li>
                <li><i className="day-icon" data-feather="sun"></i><span className="day-name">Mon</span><span className="day-temp">xx°C</span></li>
                <li ><i className="day-icon" data-feather="sun"></i><span className="day-name">Tue</span><span className="day-temp">xx°C</span></li>
                <li><i className="day-icon" data-feather="cloud"></i><span className="day-name">Wed</span><span className="day-temp">xx°C</span></li>
                <li><i className="day-icon" data-feather="cloud-snow"></i><span className="day-name">Thu</span><span className="day-temp">xx°C</span></li>
                <li><i className="day-icon" data-feather="cloud-rain"></i><span className="day-name">Fry</span><span className="day-temp">xx°C</span></li>
                <div className="clear"></div>
              </ul>
            </div>
            <div className="location-container">
              <button className="location-button"> <i data-feather="map-pin"></i><span>Change location</span></button>
            </div>
          </div>
        </div>
    </div>
  );
}

export default App;
